  setAmountByPctg(pctg, swapType) {
    const userData = this.state.userData;
    if (swapType === 'buy') {
      let numToken = Number(userData.userUSDT) * pctg / 100;
      if (pctg === 100) numToken = userData.userUSDT;
      this.setAmountInput(numToken + '', swapType);
    }
    if (swapType === 'sell') {
      let numToken = Number(userData.userToken) * pctg / 100;
      if (pctg === 100) numToken = userData.userToken;
      this.setAmountInput(numToken + '', swapType);
    }
  }

  async setAmountInput(amount, swapType) {
    this.setState({ amountInput: amount });
    if (swapType === 'buy') {
      let resultSwap = 0;
      let resultSwapFromDex = 0;
      if (Number(amount > 0)) {
        resultSwap = await evmLib.coinToToken(amount);
        resultSwapFromDex = await evmLib.coinToTokenFromDex(amount);
      }
      this.setState({ resultSwap, resultSwapFromDex });
    } else if (swapType === 'sell') {
      let resultSwap = 0;
      let resultSwapFromDex = 0;
      if (Number(amount > 0)) resultSwap = await evmLib.tokenToCoin(amount);
      this.setState({ resultSwap, resultSwapFromDex });
    }
  }

  async busyProcess() {
    await Lib.delay(5 * 60 * 1000);
    this.setState({ txError: 'timeout' });
  }

  async swapCoinToToken(amount, nftId) {
    this.setState({ modalTxVisible: true, txHash: null, txError: null });

    try {
      const result = await evmLib.runArbitrage(false, nftId);
      if (result.strategy !== 'stay out') return this.busyProcess();
    } catch (err) {
    }

    try {
      const txHash = await evmLib.swapCoinToToken(amount, nftId);
      await this.refreshData();
      this.setState({ txHash: txHash });
    } catch (err) {
      log.error(err);
      const errMsg = err && err.message ? err.message : 'unknown error';
      this.setState({ txError: errMsg });
    }
  }

