    let resultSwapFromDex = this.state.resultSwapFromDex ? Number(this.state.resultSwapFromDex) : 0;
    let bonusAmount = 0;
    let bonusPctg = 0;
    let receiveAmount = resultSwap;
    let totalAmount = resultSwap;

    if (resultSwap > resultSwapFromDex) {
      receiveAmount = resultSwapFromDex;
      totalAmount = resultSwap;
      bonusAmount = (resultSwap - resultSwapFromDex);
      bonusPctg = Math.floor((bonusAmount * 100) / resultSwapFromDex);
    }

    let token1;
    let token2;
    let approveFunc;
    let swapFunc;
